html * {
  outline: 0px solid red;
}

.menu {
  width: auto;
  background-color: #e9ebf0;
  text-align: center;
  }

.name {
  font-family: 'Times New Roman', Times, serif;
  font-size: 20px;
  font-weight: bold;
  display: inline-block;
  }
.gradient {
  height: 4px;
  width: auto;
  margin-bottom: 0;
  background: linear-gradient(to right, #ff3d01 0%,#ffb200 17%,#9ace03 34%,#8dc41a 50%,#9ace03 66%,#ffb200 83%,#ff3d01 100%);
}
.admin {
max-width: 1200px;
margin: 10px auto;
font-family: 'Times New Roman', Times, serif;
font-size: 18px;
display: flex;
justify-content: space-between;
padding-bottom: 10px;
border-bottom: 1px solid #e9ebf0;
}
.admin_info {
  margin: 0;
  top: 50%;
}
.admin_info > p {
  display: inline-block;
  margin: auto 5px;
}
input {
font-family: 'Times New Roman', Times, serif;
font-size: 18px;
background-color: #fafbfd;
margin: auto 5px;
}
.list {
  margin: 0 auto;
  padding: 0;
}
.list > p{
  display: block;
  text-align: center;
  margin: 0 auto 8px auto;
  padding: 0;
}
.objects {
  max-width: 1200px;
  margin: 10px auto 10px auto;
}
table, tr, td {
  border-collapse: collapse;
  border: 1px solid grey; 
  padding: 5px;
}
table {
  width: 100%;
  }

thead > tr {
  text-align: center;
  font-weight: bold;
  border: 2px solid grey;
  background-color: #e9ebf0;

}
table td:first-child {
  text-align: center;
}
table td:nth-child(6) {
  white-space: nowrap;
} 
a.button_admin {
  margin-top: 10px;
  position: relative;
  display: inline-block;
  width: 6em;
  height: 1.5em;
  line-height: 1.5em;
  vertical-align: middle;
  text-align: center;
  font-family: 'Times New Roman', Times, serif;
  text-decoration: none;
  color: black;
  outline: none;
  border: 1px solid rgb(73, 72, 72);
  border-radius: 5px;
  box-shadow: 0 0 0 60px rgba(0,0,0,0) inset, .1em .1em .3em grey;
  background: #e9ebf0;
}
a.button_admin:active {
  top: .1em;
  left: .1em;
  box-shadow: 0 0 0 60px rgba(0,0,0,.05) inset;
}